import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AlarmConfigurationCrudComponent } from './alarm-configuration-crud//alarm-configuration-crud.component';
import { TABS_DEMO_ROUTES } from './alarm-configuration-crud/routes';

const routes : Routes = [
    {
        path : 'crud',component : AlarmConfigurationCrudComponent, children : TABS_DEMO_ROUTES
    }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class AlarmConfigurationRoutingModule{}