
import {Component, OnInit, ViewChild,ChangeDetectorRef} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { MatDialog,MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ChargingModelService } from 'app/shared/services/charging-model/charging-model.service';
import {MessageConstant} from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import {FuseConfirmDialogComponent} from '@fuse/components/confirm-dialog/confirm-dialog.component';
import{ViewDialogComponent} from'../view-dialog/view-dialog.component';
import * as XLSX from 'xlsx';
export class ChargingModel {
  id : number;
  client_id : number;
  client_name : string;
  manufacturer_id : number ;
  manufacturer_name : string ;
  charger_model_type_id : number;
  charger_model_type_name : string;
  battery_backup : string;
  code : string;
  name : string;
  charger_type_name:string;
  description : string;
  communication_protocol_id : number;
  communication_protocol_name : string;
  communication_mode : string;
  card_reader_type : string;
  no_of_connectors : number;
  status : string ;
  created_date : string ;
  created_by : number;
  modify_date : string ;
  modify_by : number;
  connector_data : string;
  statusString:string;
}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class ListComponent implements OnInit {
  filterEntity: ChargingModel;
  filterType: MatTableFilter;
  displayedColumns: string[] = ['code','name', 'charger_type_name','manufacturer_name','status','action'];

  dataSource: MatTableDataSource<ChargingModel>;

  charginModels : ChargingModel[];
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  dialogRef: any;
  codeSearch = false;
  nameSearch = false;
  chargerTypeSearch = false;
  manfactureSearch = false;
  cModelTypeSearch = false;
  batteryBackupSearch = false;
  communicationSearch = false;
  communicationModeSearch = false;
  cardReaderSearch = false;
  noConnectorSearch = false;
  statusSearch = false;
  exportedData: any[];
  //messageConstant = MessageConstant;
  constructor(private chargingModelService : ChargingModelService ,public dialog : MatDialog,
    private snackBar : MatSnackBar,private cdr: ChangeDetectorRef) {
  }

  ngOnInit() {
    
    
    this.getChargingModels();
    this.filterEntity = new ChargingModel();
           this.filterType = MatTableFilter.ANYWHERE;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getChargingModels(){
    this.chargingModelService.getChargingModelsAll().subscribe(
      (data:any)=>{
        //;
        if(data != null){
          this.charginModels = data;
          
          for(var i=0; i < this.charginModels.length;i++){
            if(this.charginModels[i].status == 'N'){
              this.charginModels[i].statusString = 'Inactive';
            }else{
              this.charginModels[i].statusString = 'Active';
            }
            
           
          }
          this.dataSource = new MatTableDataSource(this.charginModels);
  
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
          this.cdr.detectChanges();
        }else{
          return;
        }
       
      },(err)=>{
        
      }
    )
  }

  delete(id){
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
  });

  //this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

  this.confirmDialogRef.afterClosed().subscribe(result => {
      if ( result )
      {
          this.chargingModelService.delete(id).subscribe(
      (data:any)=>{
        ;
        this.getChargingModels();
        this.snackBar.open(MessageConstant.deleteMessage,'Dismiss',{duration:2000});
        this.cdr.detectChanges();
      },(err)=>{
        
      }
    )
      }
      this.confirmDialogRef = null;
  });
   
  }

  viewDialog(action,data) : void{
    let params = {
      action : action,
      data : data
    }

    const dialogRef = this.dialog.open(ViewDialogComponent, {
      width: '1400px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true    
    });

    dialogRef.afterClosed().subscribe(result => {
      
      // this.moderate(result);
      this.getChargingModels();
    });
  }
  openDialog(action,data): void {
    // this.snackBar.open('Unable to proceed, In prgress','Dismiss');
    // return false;
    let params = {
      action : action,
      data : data
    }

    const dialogRef = this.dialog.open(ActionDialogComponent, {
      width: '1400px',
      data: params,
      autoFocus: false,
      disableClose: true,
      panelClass: 'app-action-dialog',
    });

    dialogRef.afterClosed().subscribe(result => {
      this.getChargingModels();
      this.cdr.detectChanges();
    });
  }

  changeVisibility(visibleOption,ev){
    // visibleOption = !visibleOption;
    //nameSearch = false;
      //descSearch = false;
      ev.stopPropagation();
      if(visibleOption == 'codeSearch'){
        this.codeSearch = !this.codeSearch;
        this.filterEntity.code = "";
      }
    if(visibleOption == 'nameSearch'){
      this.nameSearch = !this.nameSearch;
      this.filterEntity.name = "";
    }
    if(visibleOption == 'chargerTypeSearch'){
      this.chargerTypeSearch = !this.chargerTypeSearch;
      this.filterEntity.charger_type_name = "";
    }
    if(visibleOption == 'manfactureSearch'){
      this.manfactureSearch = !this.manfactureSearch;
      this.filterEntity.manufacturer_name = "";
    }
    if(visibleOption == 'cModelTypeSearch'){
      this.cModelTypeSearch = !this.cModelTypeSearch;
      this.filterEntity.charger_type_name = "";
    }
    if(visibleOption == 'batteryBackupSearch'){
      this.batteryBackupSearch = !this.batteryBackupSearch;
      this.filterEntity.battery_backup = "";
    }
    if(visibleOption == 'communicationSearch'){
      this.communicationSearch = !this.communicationSearch;
      this.filterEntity.communication_protocol_name = "";
    }
    if(visibleOption == 'communicationModeSearch'){
      this.communicationModeSearch = !this.communicationModeSearch;
      this.filterEntity.communication_mode = "";
    }
    if(visibleOption == 'cardReaderSearch'){
      this.cardReaderSearch = !this.cardReaderSearch;
      this.filterEntity.card_reader_type = "";
    }
    if(visibleOption == 'noConnectorSearch'){
      this.noConnectorSearch = !this.noConnectorSearch;
      this.filterEntity.no_of_connectors= null;
    } 
    if(visibleOption == 'statusSearch'){
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString = "";
    }
   }
   tableExport()
   {
     // const tab = document.getElementById('mapTable');
     // const ws: XLSX.WorkSheet=XLSX.utils.table_to_sheet(tab);//converts a DOM TABLE element to a worksheet
     // const wb: XLSX.WorkBook = XLSX.utils.book_new();
     // XLSX.utils.book_append_sheet(wb, ws, 'Mapped Charger');
   
     // /* save to file */
     // XLSX.writeFile(wb, 'Mapped Charger.xlsx');
     //this.preloader.open();
     this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
       var chargerModel = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = {'Code':chargerModel.code,'Name':chargerModel.name,'Charger Type':chargerModel.charger_type_name,
                  'Manufacturer': chargerModel.manufacturer_name,'Charger Model Type ':chargerModel.charger_model_type_name,
                  'Battery Backup':chargerModel.battery_backup,'Communication Protocol':chargerModel.communication_protocol_name,
                  'Communication Mode':chargerModel.communication_mode,'Card Reader Type':chargerModel.card_reader_type,
                  'No Of Connectors':chargerModel.no_of_connectors,'Status':chargerModel.statusString};
      this.exportedData.push(data);
    }


     const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
       const workBook: XLSX.WorkBook = XLSX.utils.book_new();
       XLSX.utils.book_append_sheet(workBook, workSheet, 'SheetName');
       XLSX.writeFile(workBook, 'Varients.xlsx');
     //XLSX.writeFileAsync('OCPP Logs.xlsx',workBook, this.cb)
      //this.preloader.close();
     
   
   }
}

