  getClient(){
    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
    this.clientService.getActiveClientsCW(this.localStorage.userId).subscribe(
      (data:any)=>{
        this.clients = data;
        this.filteredOptionsClient = this.form.controls.client.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterClients(name) : this.clients)
        );
      },(err)=>{
        
      }
    )
  }
  getChargingModel(){
    this.masterService.getChargerModelTypes().subscribe(
      (data:any)=>{
        this.chargerModelTypes = data;
        this.filteredOptionsModel = this.form.controls.chargerModelType.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterModel(name) : this.chargerModelTypes)
        );
      },(err)=>{
      }
    )
  }
  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }

  


   displayModel(item: any): string {
    return item && item.name ? item.name : '';
  }
  displayClient(item: any): string {
    return item && item.name ? item.name : '';
  }

  private _filterModel(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.chargerModelTypes.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  private _filterClients(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.clients.filter(option => option.name.toLowerCase().includes(filterValue));
  }

